import Head from 'next/head';
import Layout from '../public/components/layout.js';
import MovieCard from '../public/components/movieCard.js';
import fetch from 'node-fetch';
import ls from 'local-storage';

export async function getStaticProps() {
/*   ls.set('results', null); */
  let data = ls.get('results');
  if(data === null){
    const res = await fetch('https://api.themoviedb.org/3/trending/all/week?api_key=a7c72915d9ca22d06835063429d58c63');
    const tmp = await res.json();
    ls.set('results', tmp, 60000 );
    data = tmp;
    console.log("Cache empty, reading from API");
  }
  else{
    console.log("Cache not empty, reading from cache:")
  }




  // By returning { props: posts }, the Blog component
  // will receive `posts` as a prop at build time
  return {
    props: {
      data,
    },
  }
}

class Header extends React.Component{
  constructor(props){
    super(props);
    this.state.refresh = false;
  }

  onclick = () => {
    ls.set('results', null);
    this.setState({refresh: !this.state.refresh});
    
  }
}

